.login__wrapper {
  margin: auto;
  padding: 10px; }

.login__card {
  background-color: #ffffff;
  padding: 50px 60px;
  max-width: 520px;
  width: 100%; }
  .theme-light .login__card {
    background-color: white; }
  .theme-dark .login__card {
    background-color: #232329; }

.login__profile {
  text-align: center; }

.login__btns {
  display: flex;
  width: calc(100% + 10px);
  margin: -10px 0 -20px -10px; }
  .login__btns a {
    margin: 10px 0 20px 10px;
    white-space: nowrap; }

.login__btn {
  width: 100%;
  margin-right: 0; }

.login__avatar {
  height: 64px;
  width: 64px;
  border-radius: 50%;
  overflow: hidden; }

.login__name {
  font-size: 11px;
  text-transform: uppercase;
  font-weight: 700;
  line-height: 15px;
  margin-top: 5px; }

.login__sub {
  margin-top: 0;
  margin-bottom: 10px;
  color: #999999;
  font-size: 11px;
  line-height: 15px; }

a.login__forgot-password {
  position: absolute;
  right: 0;
  color: #ffffff;
  margin: 0 0 0.5rem; }
  a.login__forgot-password:hover {
    color: #ff4861;
    text-decoration: none; }

.form__form-group--forgot {
  margin-bottom: 40px; }

.login__or {
  text-align: center;
  margin-top: 35px;
  margin-bottom: 20px;
  position: relative; }
  .login__or:before, .login__or:after {
    content: "";
    height: 1px;
    width: calc(50% - 90px);
    background: #dddddd;
    position: absolute;
    top: 10px; }
  .login__or:before {
    left: 0; }
  .login__or:after {
    right: 0; }

.login__social {
  text-align: center; }

.login__social-btn {
  display: inline-block;
  height: 38px;
  width: 38px;
  border-radius: 5px;
  padding: 9px;
  margin-right: 10px;
  transition: all 0.3s;
  border: none; }
  .login__social-btn:last-child {
    margin-right: 0; }
  .login__social-btn svg {
    fill: #ffffff;
    height: 20px;
    width: 20px; }
  .login__social-btn.login__social-btn--facebook {
    background: #4766a4; }
    .login__social-btn.login__social-btn--facebook:hover {
      background: #385080; }
  .login__social-btn.login__social-btn--google {
    background: #c74d4d; }
    .login__social-btn.login__social-btn--google:hover {
      background: #ab3636; }

.login__head {
  margin-bottom: 30px;
  padding-left: 10px;
  border-left: 4px solid #70bbfd; }

.login__logo {
  font-weight: 700; }

.login__logo-accent {
  color: #70bbfd; }

.login__have-login {
  text-align: center;
  margin-top: 20px; }
  .login__have-login a {
    color: #70bbfd;
    transition: all 0.3s; }
    .login__have-login a:hover {
      color: #3ea3fc;
      text-decoration: none; }

